import React, { useState, useEffect } from 'react';
import { Card, Divider, Button, Space, message } from 'antd';
import { PurchaseGoodsListComponent } from './purchase_goods_list';
// import { ShowPurchaseDeliveryAddress } from './purchase_delivery_address';
// import InfiniteScroll from 'react-infinite-scroller';
import customConfig from '../../../custom_config'
// import { Loading3QuartersOutlined } from '@ant-design/icons';
import { DeliveryAddressIndexComponent } from '../delivery_address_components/delivery_address_index';
// import { set } from 'js-cookie';
import { PurchaseRecordPrintComponent } from '../print_purchase_record/purchase_record_print';
import { customerShowName } from './purchase_records_common_funcs';

const order_state_obj = customConfig.purchaseRecordStates;
const order_state_color_obj = customConfig.purchaseRecordStatesColor;

const PurchaseRecordCard = ({ index, purchaseRecordObj, infoBottonTips, infoBottonClick, customBottonTips, customBottonClick, printerIndex, successPrintHandle }) => {
    // 购买记录单个卡片组件
    const [recordTitle, setRecordTitle] = useState('');
    const [orderState, setOrderState] = useState(0);
    const [purchaseGoodsList, setPurchaseGoodsList] = useState([]);
    // const [deliveryAddress, setDeliveryAddress] = useState({});
    const [purchaseRecordCreateTime, setPurchaseRecordCreateTime] = useState('');
    const [purchaseAmounts, setPurchaseAmounts] = useState(0);
    const [explainsCustomer, setExplainsCustomer] = useState('');
    const [explains, setExplains] = useState('');
    const [purchaseRecordCardStyle, setPurchaseRecordCardStyle] = useState({});

    const createPurchaseRecordCardStyle = (purchaseRecordState) => {
        // 创建购买记录卡片样式
        let baseStyle = {};
        if (purchaseRecordState >= 0 && purchaseRecordState < 6) {
            baseStyle = {'backgroundColor': order_state_color_obj[purchaseRecordState]}
        }
        return baseStyle;
    }

    useEffect(() => {
        let customerObj = {};
        let cardTitle = `订单ID:${purchaseRecordObj.id}; `;
        let currentCustomerName = '';
        if (Object.keys(purchaseRecordObj).includes('customer_info')) {
            customerObj = purchaseRecordObj.customer_info;
            currentCustomerName = customerShowName(customerObj);
        }
        cardTitle = Object.keys(customerObj).includes('name') && currentCustomerName.length > 0 ? (cardTitle + `顾客:${currentCustomerName}; `) : cardTitle;
        // cardTitle = purchaseRecordObj.order_state > -1 ? (`${cardTitle} 订单状态:${order_state[purchaseRecordObj.order_state]}`) : cardTitle;
        setOrderState(purchaseRecordObj.order_state);
        setPurchaseRecordCardStyle(createPurchaseRecordCardStyle(purchaseRecordObj.order_state));
        setRecordTitle(cardTitle);
        setPurchaseGoodsList(purchaseRecordObj.goods);
        // setDeliveryAddress(purchaseRecordObj.delivery_address);
        setPurchaseRecordCreateTime(purchaseRecordObj.add_time);
        setPurchaseAmounts(purchaseRecordObj.purchase_amount);
        setExplainsCustomer(purchaseRecordObj.explains_customer);
        setExplains(purchaseRecordObj.explains);
        // console.log(purchaseRecordObj);
    }, [purchaseRecordObj]);

    const [actionsBottons, setActionsBottons] = useState([])
    useEffect(() => {
        let bottons = [<Button type="link" onClick={() => infoBottonClick(purchaseRecordObj, index)}>{infoBottonTips}</Button>];
        if (customBottonTips.length > 0) {
            bottons.push(<Button type="link" onClick={() => customBottonClick(purchaseRecordObj, index)}>{customBottonTips}</Button>);
        }
        setActionsBottons(bottons);
    }, [purchaseRecordObj, customBottonTips]);

    return (
        <div style={{ width: '100%', height: 'auto', borderRadius: '0.2rem', marginBottom: '20px' }}>
            <Card
                headStyle={purchaseRecordCardStyle}
                title={recordTitle}
                bordered={false}
                style={{ width: '100%', height: 'auto' }}
                actions={actionsBottons}
            >
                <Space direction="vertical">
                    <h6>订单状态: {order_state_obj[orderState]}</h6>
                    {purchaseAmounts > 0 ? <h6>订单金额: ¥{purchaseAmounts}</h6> : null}
                    <PurchaseRecordPrintComponent
                        purchaseRecordObj={purchaseRecordObj}
                        printerIndex={printerIndex}
                        successPrintHandle={successPrintHandle}
                    ></PurchaseRecordPrintComponent>
                </Space>
                <Divider></Divider>
                <Button type="link" onClick={() => message.info('功能未开通...')}>增加商品</Button>
                {purchaseGoodsList.length > 0
                    ? (<div><PurchaseGoodsListComponent purchseGoodsList={purchaseGoodsList} /></div>)
                    : null}
                {/* 收货地址组件 */}
                <Divider />
                <DeliveryAddressIndexComponent purchaseRecordObj={purchaseRecordObj}></DeliveryAddressIndexComponent>
                <Divider />
                <p>顾客留言: {explainsCustomer}</p>
                <p>下单时间: {purchaseRecordCreateTime}</p>
                {explains.length > 1 ? <div><Divider orientation="left">商家备注(仅商家自己可见)</Divider><p>{explains}</p></div> : null}
            </Card>
        </div>
    );
};
PurchaseRecordCard.defaultProps = {
    index: 0,
    purchaseRecordObj: {
        id: 0,
        customer_info: {},
        name: '',
        order_state: '',
        goods: [],
        delivery_address: {},
        add_time: '',
        purchase_amount: '',
        explains: ''
    },
    infoBottonTips: '详情',
    infoBottonClick: (purchaseRecordObj, index) => console.log(index, purchaseRecordObj),
    customBottonTips: '',
    customBottonClick: (purchaseRecordObj, index) => console.log(index, purchaseRecordObj),
    deliverComponent: null,
    printerIndex: -1
}

const PurchaseRecordsListComponent = ({
    purchaseRecordsList,
    loadMore,
    hasMore,
    loading,
    infoBottonTips,
    infoBottonClick,
    customBottonTips,
    customBottonClick,
    deliverButtonClick,
    deliverComponent,
    printerIndex,
    successPrintCurrentRecordHandle
}) => {
    // 购买记录列表组件
    // const [showLoadMoreButton, setShowLoadMoreButton] = useState(true);
    // useEffect(() => {
    //     console.log(hasMore);
    //     setShowLoadMoreButton(hasMore === 2);
    // }, [hasMore])

    return (
        <div style={{ padding: '0.5rem', background: '#ececec', width: '100%', height: 'auto' }}>
            {purchaseRecordsList.map((recordObj, index) => {
                return <PurchaseRecordCard key={recordObj.id.toString()}
                    index={index}
                    purchaseRecordObj={recordObj}
                    infoBottonTips={infoBottonTips}
                    infoBottonClick={infoBottonClick}
                    customBottonTips={customBottonTips}
                    customBottonClick={customBottonClick}
                    deliverButtonClick={deliverButtonClick} 
                    deliverComponent={deliverComponent}
                    printerIndex={printerIndex}
                    successPrintHandle={(hasPrintPurchaseRecordObj) => successPrintCurrentRecordHandle(hasPrintPurchaseRecordObj, index)} />
            })}
        </div>
    );
}
PurchaseRecordsListComponent.defaultProps = {
    loadMore:() => console.log('Load more'),
    hasMore: 2,
    loading: false,
    purchaseRecordsList: [],
    infoBottonTips: '详情',
    infoBottonClick: (purchaseRecordObj, index) => console.log(index, purchaseRecordObj),
    customBottonTips: '',
    customBottonClick: (purchaseRecordObj, index) => console.log(index, purchaseRecordObj),
    deliverButtonClick: (purchaseRecordObj, index) => console.log(index, purchaseRecordObj),
    deliverComponent: null,
    printerIndex: -1,
    successPrintCurrentRecordHandle: (hasPrintPurchaseRecordObj, index) => console.log(hasPrintPurchaseRecordObj, index),
};

export {
    PurchaseRecordsListComponent,
    PurchaseRecordCard,
};
