import React, { useState, useEffect } from 'react';
import { Row, Col } from 'antd';
import { expressCodeQueryName } from '../../../assets/common_funcs/express_code_name_handle';

// 显示收货地址组件

const ShowPurchaseDeliveryAddress = ({deliveryAddressObj}) => {
    // 显示收货地址组件
    const [consignee, setConsignee] = useState('');
    const [mobile, setMobile] = useState('')
    const [fullDeliveryAddress, setFullDeliveryAddress] = useState('');
    const [expressInfo, setExpressInfo] = useState('');

    useEffect(() => {
        // console.log(deliveryAddressObj);
        setConsignee(deliveryAddressObj.consignee ? deliveryAddressObj.consignee : '');
        setMobile(deliveryAddressObj.mobile ? deliveryAddressObj.mobile : '');
        const province = deliveryAddressObj.province ? deliveryAddressObj.province : '';
        const city = deliveryAddressObj.city ? deliveryAddressObj.city : '';
        const district = deliveryAddressObj.district ? deliveryAddressObj.district : '';
        const address = deliveryAddressObj.address ? deliveryAddressObj.address : '';
        let currentDeliveryAddress = province + city + district + address;
        currentDeliveryAddress = currentDeliveryAddress.toString();
        setFullDeliveryAddress(currentDeliveryAddress);

        // 快递单号
        const expressCompanyCode = deliveryAddressObj.express_company_code ? deliveryAddressObj.express_company_code : '';
        const expressNumber = deliveryAddressObj.express_number ? deliveryAddressObj.express_number : '';
        const expressCompanyName = expressCodeQueryName(expressCompanyCode)
        setExpressInfo(`${expressCompanyName}(${expressNumber})`)
    }, [deliveryAddressObj])

    return (
        <div style={{width: '100%', height: 'auto', padding: '0.2rem'}}>
            {(consignee.length === 0 && mobile.length === 0) 
                ? null
                : (<Row><Col span={24}><h6>收件人: {`${consignee} ${mobile}`}</h6></Col></Row>)}
            {fullDeliveryAddress.length ===0
                ? null
                : (<Row><Col span={24}><p>地址: {fullDeliveryAddress}</p></Col></Row>)}
            {expressInfo.length < 5 
                ? null
                : (<Row><Col span={24}><p>快递: {expressInfo}</p></Col></Row>)}
        </div>
    )
};
ShowPurchaseDeliveryAddress.defaultProps = {
    deliveryAddressObj: {}
};

export {
    ShowPurchaseDeliveryAddress
};
