import Cookies from 'js-cookie';
import customConfig from '../../custom_config';
import { sendAxiosRequest } from '../../components/apis/sendRequest';

const expressCompanyRequestGet = () => {
    // 快递公司 Get请求
    return new Promise(function (resolve, reject) {
      const api = 'kdniao-admin/v1/kdniaoexpresscompanycodes';
      const method = 'get';
      const reqParams = {};
      const reqObject = {};
      sendAxiosRequest(api, method, reqParams, reqObject)
        .then(respObj => {
            if (Object.keys(respObj).includes('results')) {
                // JSON.stringify(data); 转json字符串
                const jsonStr = JSON.stringify(respObj.results);
                resolve(jsonStr);
            } else {
                reject(respObj);
            }
        })
        .catch(err => {
          console.log(err);
          reject(err);
        });
    });
  }

const downloadAndSaveExpressList = () => {
    // 下载快递公司编号名称列表, 保存到本地
    expressCompanyRequestGet().then(results => {
        const expressSaveKey = customConfig.expressInfoKey;
        return Cookies.set(expressSaveKey, results);
    }).catch(err => {
        console.log(err);
        return 'Error...';
    })
}

const loadExpressList = () => {
    // 读取快递公司列表
    const expressSaveKey = customConfig.expressInfoKey;
    const expressList = Cookies.get(expressSaveKey) ? Cookies.get(expressSaveKey) : '[]';
    // console.log(expressList);
    return JSON.parse(expressList);
}

const expressCodeQueryName = (expressCode) => {
    // 快递公司代码查找快递公司名称
    const expressList = loadExpressList();
    for (let index = 0; index < expressList.length; index++) {
        const element = expressList[index];
        const current_keys = Object.keys(element);
        if (current_keys.includes('express_company_name')
            && current_keys.includes('express_company_code')
            && element.express_company_code === expressCode) {
            return element.express_company_name
        }
    }
    return '';
}

export {
    downloadAndSaveExpressList,
    loadExpressList,
    expressCodeQueryName
}