import React, { useState } from 'react';
import {Row, Col, Button, Divider, message} from 'antd';
import ImageZoom from '../image_zoom';

// import { ModalSelectColorNum } from '../../wx_store/goods/modal_select_color_num';

// 购买的商品列表组件

const PurchseSingleGoodsRowComponent = ({src, title, attribute, amount, num}) => {
    //  单个商品行展示
    // const [modifyColorNumVisible, setModifyColorNumVisible] = useState(false);

    const [showDelButton, setShowDelButton] = useState(false);

    // const modifyColorNumHandle = (selectedColorObj) => {
    //     console.log(selectedColorObj);
    // }

    const openDelButton = <Button type="link" onClick={() => setShowDelButton(true)}>更多操作...</Button>
    const delButton = <Button type="link" onClick={() => message.info('删除功能未开通...')}>删除</Button>

    return (
        <div style={{padding: '0.2rem', width: '100%', height: 'auto'}}>
            {/* <ModalSelectColorNum visible={modifyColorNumVisible}
                setVisible={setModifyColorNumVisible}
                selectHandle={modifyColorNumHandle}
            ></ModalSelectColorNum> */}

            <Divider />
            <Row gutter={{ xs: 4, sm: 8, md: 16, lg: 24 }}>
                <Col span={8}>
                    {src.length > 5 ? <ImageZoom src={src}></ImageZoom> : null}
                </Col>
                <Col span={12}>
                    <Row> <Col span={24}><h6>{title}</h6></Col></Row>
                    <Row><Col span={24}><p>{attribute}</p></Col></Row>
                </Col>
                <Col span={4}>
                    <Row><Col span={24}>{amount > 0 ? <p>¥{amount}</p> : null}</Col></Row>
                    <Row><Col span={24}>{num > 0 ? <p>数量: {num}</p> : null}</Col></Row>
                    <Row><Col span={24}>
                        {showDelButton ? delButton : openDelButton}
                    </Col></Row>
                </Col>
            </Row>
        </div>
    );
}
PurchseSingleGoodsRowComponent.defaultProps = {
    src: '', title: '', attribute: '', amount: 0, num: 0
};

const PurchaseGoodsListComponent = ({purchseGoodsList}) => {
    return (
        <div>
            {purchseGoodsList.map((item, index) => {
                return (<PurchseSingleGoodsRowComponent
                            src={item.pic_url}
                            title={item.product_title}
                            attribute={item.product_color}
                            amount={item.product_amounts}
                            num={item.product_number}
                            key={index.toString()} />)
            })}
        </div>
    )
};
PurchaseGoodsListComponent.defaultProps = {
    purchseGoodsList: []
};

export {
    PurchseSingleGoodsRowComponent,
    PurchaseGoodsListComponent,
};
