// 订单通用处理方法

const customerShowName = (customerObj = {}) => {
    let wxNicknime = '', customerName = '';
    if (Object.keys(customerObj).includes('name')) {
        customerName = customerObj.name;
    }
    if (Object.keys(customerObj).includes('wx_nick_name')) {
        wxNicknime = customerObj.wx_nick_name;
    }
    const showName = wxNicknime.length > 0 ? `${customerName}(${wxNicknime})` : customerName;
    return showName;
}

export {
    customerShowName,
};
