import React, { useEffect, useState } from 'react';
import { Button } from 'antd';

import { print80LabelHandle } from '../lodop/lodop_print_handle';
import { multipleUpdatePurchaseRecordHasPrintRequest } from './purchase_record_print_request';
import { appendMetaContentSecurityPolicy } from '../../../assets/common_funcs/meta_handle.js';
import { customerShowName } from '../purchase_records_goods_delivery_address/purchase_records_common_funcs';

// 打印当前购物单

const createTextsList = (pruchaseRecordObj) => {
    // 创建订单打印数组
    // console.log(pruchaseRecordObj);
    const customerInfo = Object.keys(pruchaseRecordObj).includes('customer_info')
        ? pruchaseRecordObj.customer_info : {name: '', wx_nick_name: ''};
    const name = customerShowName(customerInfo);
    return [
        '日期: ' + pruchaseRecordObj.add_time,
        '订单ID: ' + pruchaseRecordObj.id,
        '顾客姓名: ' + name,
        '',
        '～～～ 光度 ～～～',
        'R: ' + pruchaseRecordObj.diopter_r,
        'L: ' + pruchaseRecordObj.diopter_l,
        'PD: ' + pruchaseRecordObj.pupillary_distance,
        '～～～～～～',
        '',
        '商品: ' + pruchaseRecordObj.product_title,
        '顾客留言: ' + pruchaseRecordObj.explains_customer,
        '',
        '～～～ 商家备注 ～～～ ',
        pruchaseRecordObj.explains,
        '～～～～～～',
        '',
        '商品总数量: ' + pruchaseRecordObj.products_quantity,
    ];
}

const printSinglePurchaseRecordHandle = (purchaseRecordObj, printerIndex, successPrintHandle) => {
    // 打印单个订单
    const printList = createTextsList(purchaseRecordObj);
    console.log(purchaseRecordObj);
    print80LabelHandle(printList, '展望眼镜', printerIndex, 0, 1).then(result => {
        console.log(result);
        const purchaseRecordObjId = Object.keys(purchaseRecordObj).includes('id')
            ? purchaseRecordObj.id : 0;
        if (purchaseRecordObjId > 0) {
            // 更新client端订单已打印
            successPrintHandle({ ...purchaseRecordObj, has_print: true });
        }
    }).catch(err => {
        console.log(err);
    });
};

const printMultiplePurchaseRecordsHandle = (purchaseRecordsList, printerIndex, successPrintHandle) => {
    // 批量打印订单
    const idsList = [];
    for (let index = 0; index < purchaseRecordsList.length; index++) {
        const element = purchaseRecordsList[index];
        printSinglePurchaseRecordHandle(element, printerIndex, successPrintHandle);
        if (element.id > 0) {
            idsList.push(element.id);
        }
    }
    // 更新服务器订单已打印
    multipleUpdatePurchaseRecordHasPrintRequest(idsList).then(result => {
        console.log(result);
    }).catch(err => {
        console.log(err);
    });
}

const PurchaseRecordPrintComponent = ({purchaseRecordObj, printerIndex, successPrintHandle}) => {
    const [printButtonTips, setPrintButtonTips] = useState('打印订单');
    const [printButtonStyle, setPrintButtonStyle] = useState('primary');

    useEffect(() => {
        // 页面中加入 meta 标签, 解决 HTTPS页面里动态的引入HTTP资源，被直接block掉
        appendMetaContentSecurityPolicy();
    }, []);

    useEffect(() => {
        const buttonTips = ['打印订单', '重新打印'];
        const buttonStyle = ['primary', 'default'];
        const hasPrintNum = purchaseRecordObj.has_print ? 1 : 0;
        setPrintButtonTips(buttonTips[hasPrintNum]);
        setPrintButtonStyle(buttonStyle[hasPrintNum]);
        // console.log(buttonTips, buttonStyle);
    }, [purchaseRecordObj.has_print]);

    const printHandle = () => {
        // 打印操作
        // console.log('AAA');
        printMultiplePurchaseRecordsHandle([purchaseRecordObj, ], printerIndex, successPrintHandle);
    };

    return (
        <div>
            <Button onClick={printHandle} type={printButtonStyle}>{printButtonTips}</Button>
        </div>
    )
}
PurchaseRecordPrintComponent.defaultProps = {
    purchaseRecordObj: {},
    printerIndex: -1,
    successPrintHandle: (purchaseRecordObj) => console.log('Success print purchase record'),
}

export { PurchaseRecordPrintComponent, printMultiplePurchaseRecordsHandle };
