import React, { useEffect, useState } from 'react';
import { message, Divider } from 'antd';
import { pcaa } from 'area-data'; // v3 or higher
import 'react-area-linkage/dist/index.css'; // v2 or higher
import { AreaSelect } from 'react-area-linkage';
import { CustomForm } from '../form_component';

// 修改收货地址组件

const CreateOrUpdateDeliveryAddress = ({deliveryAddressObj, setVisible, createOrUpdateAddress}) => {
    const [area, setArea] = useState({});
    const [updateFormObj, setUpdateObj] = useState({});
    const [initFormCount, setInitFormCount] = useState(0);

    useEffect(() => {
        const consignee = deliveryAddressObj.consignee;
        const mobile = deliveryAddressObj.mobile;
        const address = deliveryAddressObj.address;
        const province = deliveryAddressObj.province ? deliveryAddressObj.province : '上海市';
        const city = deliveryAddressObj.city ? deliveryAddressObj.city : '市辖区';
        const district = deliveryAddressObj.district ? deliveryAddressObj.district : '浦东新区';
        setArea([province, city, district])
        setUpdateObj({'consignee': consignee, 'mobile': mobile, 'address': address});
        // setInitFormCount(initFormCount + 1);
    }, [deliveryAddressObj]);

    const columns = ['consignee', 'mobile', 'address'];
    const formKeyLabelObj = {'consignee': '收件人', 'mobile': '手机号', 'address': '详细地址'}
    const formInitialObj = {'consignee': '', 'mobile': '', 'address': ''};
    const rulesObj = {
        "consignee": [{required: true, message: 'Please input Password!',}, ],
        "mobile": [{required: true, message: 'Please input Mobile!',}, {min: 11, max: 12, message: 'Please check Mobile!'}],
        "address": [{required: true, message: 'Please input Address!',}, ], 
    };

    const selectedAreaHandle = (val) => {
        // console.log(val);
        setArea(val);
    };

    const onFinish = (values) => {
        // 提交表单
        console.log(values, area);
        const province = area[0] ? area[0] : '';
        const city = area[1] ? area[1] : '';
        const district = area[2] ? area[2] : '';
        if (province.length === 0 || city.length === 0 || district.length === 0) {
            message.error('请先选择省市县');
            return;
        }
        const areaObj = {province, city, district};
        createOrUpdateAddress({...deliveryAddressObj, ...areaObj, ...values});
    }

    return (
        <div>
            <h5>请选择省市县</h5>
            <AreaSelect
                type="text"
                onChange={selectedAreaHandle}
                level={2}
                data={pcaa}
                defaultArea={area}></AreaSelect>
            <Divider />
            <CustomForm
                columns={columns}
                formKeyLabelObj={formKeyLabelObj}
                onFinish={onFinish}
                cancelHandle={() => setVisible(0)}
                updateFormObj={updateFormObj}
                formInitialObj={formInitialObj}
                initFormCount={initFormCount}
                rulesObj={rulesObj}
            ></CustomForm>
        </div>
    )
}
CreateOrUpdateDeliveryAddress.defaultProps = {
    deliveryAddressObj:{},
    setVisible: booleanVal => console.log(booleanVal),
    createOrUpdateAddress: formObj => console.log(formObj),
}

export {
    CreateOrUpdateDeliveryAddress,
};
