const expressCompanyRequestGet = () => {
  // 快递公司 Get请求
  return new Promise(function (resolve, reject) {
    const api = 'kdniao-admin/v1/kdniaoexpresscompanycodes';
    const method = 'get';
    const reqParams = {};
    const reqObject = {};
    sendAxiosRequest(api, method, reqParams, reqObject)
      .then(respObj => {
        resolve(respObj);
      })
      .catch(err => {
        console.log(err);
        reject(err);
      });
  });
}

export {
  DeliverRequestGet,
  DeliverRequestPost,
  DeliverRequestPut,
  DeliverRequestDelete,
  expressCompanyRequestGet
};
