// import React from 'react';
// import AxiosRequest from '../../apis';
import { sendAxiosRequest } from '../../apis/sendRequest';

const  apiLists = ['records-admin/v1/purchaserecord/', 'records-admin/v1/multipleupdatepurchaserecordshasprint'];

// 订单打印后 request 请求

const updatePurchaseRecordHasPrintRequest = (purchaseRecordId) => {
    // 更新数据订单 已打印
    return new Promise(function (resolve, reject) {
        const api = [].concat(apiLists)[0] + purchaseRecordId;
        const method = 'put';
        const reqParams = {};
        const reqObject = {has_print: true};
        sendAxiosRequest(api, method, reqParams, reqObject)
            .then(respObj => {
                resolve(respObj);
            })
            .catch(err => {
                console.log(err);
                reject(err);
            });
    });
};

const multipleUpdatePurchaseRecordHasPrintRequest = (purchaseRecordIds) => {
    // 批量更新订单已打印 purchaseRecordIds=订单id 数组
    return new Promise(function (resolve, reject) {
        const api = [].concat(apiLists)[1];
        const method = 'post';
        const reqParams = {};
        const reqObject = {ids: purchaseRecordIds};
        sendAxiosRequest(api, method, reqParams, reqObject)
            .then(respObj => {
                resolve(respObj);
            })
            .catch(err => {
                console.log(err);
                reject(err);
            });
    });
}

export { updatePurchaseRecordHasPrintRequest, multipleUpdatePurchaseRecordHasPrintRequest };
