import React, { useEffect, useState } from 'react';
import { CustomForm } from '../form_component';
import { CustomSelect } from '../select_component';
import { loadExpressList } from '../../../assets/common_funcs/express_code_name_handle';
import { currentTimestamp } from '../../../assets/common_funcs/date_handle.js';

//  ~~~~~~ 选择框, 根据实际添加 ~~~~~~
const SelectExpressCompany = ({ value = '', onChange = e => console.log(e) }) => {
    const [selectOptions, setSelectOptions] = useState([]);
    useEffect(() => {
        const resList = loadExpressList();
        console.log(resList);
        let updateOptions = [];
        for (let index = 0; index < resList.length; index++) {
            const element = resList[index];
            updateOptions.push({
            value: element.express_company_code,
            label: element.express_company_name
            })
        }
        setSelectOptions(updateOptions);
    }, []);
    return (<CustomSelect options={selectOptions} value={value} onChange={onChange}></CustomSelect>)
};
// const SelectEndTime = (<DatePicker showTime format="YYYY-MM-DD HH:mm:ss" />);
// const SelectBoolean = (<CustomSelect options={[{ value: 0, label: '否' }, { value: 1, label: '是' }]} />);
const formKeySelectComponentsObj = {
    'express_company_code': <SelectExpressCompany />,
    // exampleColumn: SelectBoolean,
};

// 填写快递单号
const DeliverHandle = ({deliveryAddressObj, setVisible, submitHandle}) => {
    const [initFormCount, setInitFormCount] = useState(0);
    const [initFormObj, setInitFormObj] = useState({});

    const columns = ['express_company_code', 'express_number'];
    const formKeyLabelObj = {'express_company_code': '快递公司', 'express_number': '快递单号'}
    const rulesObj = {
        express_company_code: [{required: true, message: 'Please select express company!',}],
        express_number: [{required: true, message: 'Please input express number!',}],
    }

    useEffect(() => {
        const expressCompanyCode = deliveryAddressObj.express_company_code;
        const expressNumber = deliveryAddressObj.express_number;
        setInitFormObj({'express_company_code': expressCompanyCode, 'express_number': expressNumber});
        setInitFormCount(initFormCount + 1);
    }, [deliveryAddressObj]);

    const onFinish = (values) => {
        const updatedObj = { ...deliveryAddressObj, ...values };
        if (updatedObj.delivery_time === 0) {
            // 更新发货时间戳为当前时间戳(秒级)
            updatedObj.delivery_time = currentTimestamp()
        }
        console.log(updatedObj);
        // 提交快递单号
        submitHandle(updatedObj);
    }

    return (
        <CustomForm
            columns={columns}
            formKeyLabelObj={formKeyLabelObj}
            formKeySelectComponentsObj={formKeySelectComponentsObj}
            // formKeysItemButtonObj,
            onFinish={onFinish}
            onFinishButtonTips="确定"
            cancelHandle={() => setVisible(0)}
            showCancelButton={true}
            updateFormObj={initFormObj}
            formInitialObj={initFormObj}
            initFormCount={initFormCount}
            rulesObj={rulesObj}
        ></CustomForm>
    )
}
DeliverHandle.defaultProps = {
    deliveryAddressObj: {},
    setVisible: (booleanValue) => console.log(booleanValue),
}

export {
    DeliverHandle
};
