import React, { useEffect, useState, useRef } from 'react';
import { Button, Space } from 'antd';
import {
    // DeliverRequestGet,
    DeliverRequestPost,
    DeliverRequestPut,
    // DeliverRequestDelete,
  } from './delivery_address_requests';

import { ShowPurchaseDeliveryAddress } from './show_delivery_address';
import { CreateOrUpdateDeliveryAddress } from './create_or_update_delivery_address';
import { DeliverHandle } from './deliver_handle';

const DeliveryAddressIndexComponent = ({purchaseRecordObj}) => {
    // 订单地址发货组件
    // showWhichComponent: 0-显示收货地址，1-显示修改收货地址，2-显示输入或修改快递单号
    const [showWhichComponent, setShowWhichComponent] = useState(0);
    const [deliveryAddressObj, setDeliveryAddressObj] = useState({});

    const createOrUpdateDeliveryAddressObj = useRef({});
    const [postRequestCount, setPostRequestCount] = useState(0);
    const [putRequestCount, setPutRequestCount] = useState(0);

    useEffect(() => {
        // 订单编号更改，修改收货地址的值
        // console.log(purchaseRecordObj);
        const baseDeliveryAddressObj = {
            purchase_record: purchaseRecordObj.id ? purchaseRecordObj.id : 0,
            customer_id:  purchaseRecordObj.customer_id ? purchaseRecordObj.customer_id : 0,
        }
        setDeliveryAddressObj(baseDeliveryAddressObj);
        if (Object.keys(purchaseRecordObj).includes('delivery_address')) {
            const newDeliveryAddressObj = {...baseDeliveryAddressObj, ...purchaseRecordObj.delivery_address};
            // console.log(newDeliveryAddressObj);
            setDeliveryAddressObj(newDeliveryAddressObj);
        }
    }, [purchaseRecordObj]);

    const createOrUpdateAddressDeliver = (addressObj) => {
        // 创建或修改地址发货
        console.log(addressObj);
        createOrUpdateDeliveryAddressObj.current = addressObj;
        if (Object.keys(addressObj).includes('id') && addressObj.id > 0) {
            // 有 id 且 id > 0 更新
            setPutRequestCount(putRequestCount + 1);
            console.log('Put count: ', putRequestCount);
        } else {
            // 否则创建
            setPostRequestCount(postRequestCount + 1);
            console.log('Post count: ', putRequestCount);
        }
    }

    const responseDataHandle = (respObj) => {
        // 新建或更新收货地址后操作
        setShowWhichComponent(0);
        console.log(respObj);
        if (Object.keys(respObj).includes('code') && respObj.code === 1000) {
            setDeliveryAddressObj(respObj);
        } else {
            console.log('Create or update error...');
        }
    }

    return (
        <div>
            {showWhichComponent === 0 
                ? (<div>
                    <Space>
                        <Button type="link" onClick={() => setShowWhichComponent(1)}>创建/更新地址</Button>
                        <Button type="link" onClick={() => setShowWhichComponent(2)}>创建/更新快递</Button>
                    </Space>
                    <ShowPurchaseDeliveryAddress deliveryAddressObj={deliveryAddressObj} />
                </div>) : null}
            {showWhichComponent === 1 ? <CreateOrUpdateDeliveryAddress
                deliveryAddressObj={deliveryAddressObj}
                setVisible={setShowWhichComponent}
                createOrUpdateAddress={createOrUpdateAddressDeliver} /> : null}
            {showWhichComponent === 2 ? <DeliverHandle
                deliveryAddressObj={deliveryAddressObj}
                setVisible={setShowWhichComponent}
                submitHandle={createOrUpdateAddressDeliver} /> : null}
            <DeliverRequestPost
                requestCount={postRequestCount}
                reqObject={createOrUpdateDeliveryAddressObj.current}
                responseDataHandle={responseDataHandle}></DeliverRequestPost>
            <DeliverRequestPut
                requestCount={putRequestCount}
                reqObject={createOrUpdateDeliveryAddressObj.current}
                responseDataHandle={responseDataHandle}
                id={deliveryAddressObj.id ? deliveryAddressObj.id : 0}></DeliverRequestPut>
        </div>
    )
};
DeliveryAddressIndexComponent.defaultProps = {
    purchaseRecordObj: {}
};

export { DeliveryAddressIndexComponent };
